/* 0. Mini SmartPhones ELEJE*/
@media (min-width: 120px) and (max-width: 319px) {

    .BaseSzovegEgeszUgy {
        display:flex;
        flex-direction: column;
        text-align: center;  
        /*border: 3px dashed red;*/
        max-height: 19rem;      
    }   

    .CsomagCimUgy {
        display:flex;
        justify-content: center;
        color: rgba(47,218,241,.87);
        color:rgba(40, 164, 241, 0.938);
        font-size: 1.4rem;
        font-weight: 900;
        padding-bottom: 5%;
        /*border:3px solid red;*/
    }

    .CsomagCimUgy h1 {    
        padding-top: 1rem;
         border: 2px solid rgba(47,218,241,.87);
         border:2px solid rgba(40, 164, 241, 0.938);
         border-radius: 3%;
         width:12rem;
         height: 3rem;
         padding: 0;
         padding-top: 2%;
         margin: 0;
    }   

    .keretUgy {
        border-radius: 20px;
        border: 1px solid rgba(47,218,241,.87);
        border:1px solid rgba(40, 164, 241, 0.938);
        padding-left:2%;
        padding-right: 2%;
        padding-bottom: 3%;
    }

    .CsomagArUgy {
        display:flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        line-height: 1.5rem;
        font-size: 1.6rem;
        font-weight: 900;
        padding-top:3%;
        padding-bottom: 5%;
        padding-left:2%;
        padding-right:2%;
        padding-top: 2rem;
       /* border: 3px solid red;*/
        min-height: 2rem;
        max-height: 2rem;
      /*  min-width: 25rem;
        max-width: 25rem;*/    
    }    

    .CsomagLeirasUgy {
        max-width: 100%;       
        min-height: 9rem;
        max-height: 9rem;
         /* margin-top: 2%;*/
       /*padding-bottom: 4%;*/
       /* border: 3px solid red;*/       
        padding:0;
        padding-top: 5%;
        margin:0;
    }  

    .SzovegUgy {
       /* letter-spacing: -1.5px;
        line-height: 3rem;*/
      /*  width:25rem;*/
        /*border: 3px solid yellow;*/
        text-align: left; 
        line-height: 1rem;
        font-size: 0.9rem; 
        letter-spacing: -0.5px; 
        padding:0;
        margin:0;
    }  

    .EredUgyAr {
        text-decoration-line: line-through;
    }

   .CsokUgyAr {
       color:red;
   }  

}

